.form {
  display: flex;

  .form__left-side {

    display: flex;
    flex-direction: column;
    width: 300px;
    align-items: center;
    padding: 3rem 3rem 3rem 21rem;

    img {
      width: 200px;
    }

    .form__left-side__details {
      margin-top: 10px;
      border: 1px solid #A03232;
      height: auto;
      width: 350px;

      .form__left-side__details__part {
        margin-left: 1rem;

        .form__left-side__details__title {
          margin-left: -1rem;
          margin-bottom: 2rem;
          text-align: center;
          font-weight: bold;
        }

        .form__left-side__details__body {

          display: flex;

          .form__left-side__details__sub-title {
            margin-top: 0;
            font-weight: bold;
          }

          .form__left-side__details__sub-body {
            margin-top: 0;
            margin-left: 3px;
          }
        }

        .form__left-side__details__body-inputs {
          display: flex;
          font-weight: bold;
          margin-bottom: 1rem;

          .form__left-side__details__body-hour__input {
            padding: 0.25rem 3.8rem;
            margin-left: 86px;
            font-size: inherit;
            font-family: inherit;
          }

          .form__left-side__details__body-hour__input4 {
            padding: 0.375rem 0.75rem;
            margin-left: 5px;
            font-size: inherit;
            font-family: inherit;
          }

          .form__left-side__details__body-hour__input2 {
            padding: 0.375rem 0.95rem;
            margin-left: 38px;
            font-size: inherit;
            font-family: inherit;
          }

          .form__left-side__details__body-hour__input3 {
            padding: 0.375rem 0.95rem;
            margin-left: 19px;
            font-size: inherit;
            font-family: inherit;
          }
        }

        .form__left-side__details__body-car {
          padding-bottom: 1rem;
          margin-left: -25px;
          width: fit-content;
          display: flex;

          .form__left-side__details__body-car__attr {
            text-align: center;

            .form__left-side__details__body-car__type {
              font-size: 18px;
              font-weight: bold;
              margin-top: 0;
            }

            .form__left-side__details__body-car__price {
              font-size: 12px;
              font-weight: lighter;
            }
          }
        }
      }
    }
  }

  .form__right-side {
    display: flex;
    padding-top: 3rem;
    padding-right: 3rem;
    flex-direction: column;
    margin-top: 100px;
    width: 40%;

    .form__right-side__inputs {

      display: grid;
      margin-bottom: 20px;

      label {
        margin-bottom: 5px;
      }

      input, textarea {
        line-height: 1.5;
        border-style: solid;
        border-radius: 0.25rem;
        border-width: 1px;
        padding: 0.5rem;
        font-size: 14px;
        border-color: rgb(206, 212, 218);
        color: rgb(73, 80, 87);
      }
    }

    .form__right-side__button {
      .form__right-side__buttons {
        width: 100%;
        border-style: solid;
        border-radius: 0.25rem;
        border-width: 1px;
        border-color: rgb(206, 212, 218);
        font-size: 14px;
        line-height: 1.5;
        padding: 0.5rem;
        background-color: #d97c7c;
        cursor: pointer;
        transition: transform 0.3s ease-in-out;

        &:hover {
          background-color: #e06868;
        }
      }
    }
  }

  @media (max-width: 768px) {
    display: grid;
    .form__left-side {
      padding: 2rem 0;
      margin: auto;
    }

    .form__right-side {
      width: 80%;
      padding: 2rem 2rem;
      margin: auto;
    }
  }
}

.notification {
  position: fixed;
  top: 8rem;
  left: 20px;
  background-color: #4CAF50;
  color: white;
  padding: 7px;
  border-radius: 5px;
  opacity: 0;
  transform: translateX(100%);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.notification.show {
  opacity: 1;
  transform: translateX(0);
}